.Navbar {
  width: 100%;
  padding: 0.5rem 0;
  background-color: #1C3643;
}

.Navbar__brand {
  color: #ffffff;

  display: flex;
  align-items: center;
}

.Navbar__brand:hover {
  color: #ffffff;
  text-decoration: none;
}

.Navbar__brand-logo {
  margin-right: 0.5rem;
}
.Container {
  display: flex;
  height: calc(100vh - 60px);
  width: 100%;
  background: url(/reactjs/static/media/stars.0abcdfcd.svg), #1b1b25;
  justify-content: center;
  align-items: center;
  /* flex-wrap: wrap; */
}

.NotFound__description {
  color: white;
  width: 700px;
  padding: 10px 15px;
}

.NotFound__description h1 {
  text-shadow: 0.1em 0.1em #333;
}

.NotFound__description > p {
  font-size: 1.5em;
  color: #c9c9c9;
}

@media screen and (max-width: 1050px) {
  .Container img {
    width: 400px;
  }
}

@media screen and (max-width: 800px) {
  .Container {
    flex-wrap: wrap;
    text-align: center;
    flex-direction: column-reverse;
    justify-content: space-evenly;
  }
  .NotFound__description {
    width: auto;
  }
}

@media screen and (max-width: 800px) {
  .Container img {
    width: 300px;
  }
  .NotFound__description h1 {
    font-size: 2em;
  }
  .NotFound__description p {
    font-size: 1.1em;
  }
}

.Home__container {
  background: url(/reactjs/static/media/stars.0abcdfcd.svg), #1b1b25;
  width: 100%;
  height: calc(100vh - 60px);
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.Home__description {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Title {
  color: white;
  text-align: center;
}

.Description {
  text-align: center;
  color: #c9c9c9;
}

@media screen and (max-width: 1050px) {
  .Home__hero-img {
    width: 53%;
  }
}

@media screen and (max-width: 700px) {
  .Home__container {
    flex-wrap: nowrap;
    flex-direction: column;
  }
  .Home__description {
    margin-bottom: 20px;
  }
  .Home__hero-img {
    width: 70%;
  }
}

@media screen and (max-width: 350px) {
  .Home__description > p {
    font-size: 0.8em;
  }
}

.BadgeDetails__hero {
  width: 100%;
  padding: 2rem 0;
  background: url(/reactjs/static/media/stars.0abcdfcd.svg), #1b1b25;
  background-repeat: repeat;
  margin-bottom: 1rem;
  color: #ffffff;
  display: flex;
  justify-content: center;
}

.BadgeDetails__hero img {
  max-height: 100px;
}

.BadgeDetails__hero-attendant-name {
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-style: italic;
  color: aqua;
}

@media screen and (max-width: 995px) {
  .container {
    width: 100%;
  }
  .row {
    display: flex;
    flex-direction: column-reverse;
  }
  .details {
    flex-direction: column;
    width: 100%;
    text-align: center;
  }
  .item {
    margin: 5px 0;
    max-width: 100%;
  }
}

@media screen and (max-width: 650px) {
  .btn {
    width: 100%;
  }
}

.Modal {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);

  display: flex;
  flex-direction: column;
  align-items: center;
}

.Modal__container {
  position: relative;
  top: 10rem;
  background-color: #ffffff;
  padding: 1rem;
  width: 400px;
}

.Modal__close-button {
  position: absolute;
  top: 0;
  right: 0;
  border: 0;
  background-color: #f5f5f5;
  padding: 0.5rem 1rem;
}

